<template>
    <div style="float: left">
    <uploader :options="options" class="uploader-example">
    <uploader-unsupport></uploader-unsupport>
      <uploader-btn>选择文件</uploader-btn>
      <uploader-btn :attrs="attrs">选择图片</uploader-btn>
      <uploader-btn :directory="true">选择文件夹</uploader-btn>
      <el-input v-model="path" placeholder="" style="width: 600px; margin-left: 330px" size="mini" readonly >
        <template slot="prepend">当前上传路径</template>
      </el-input>
    <uploader-list></uploader-list>
  </uploader>
  </div>
  
</template>

<script>
import Bus from '@/router/store.js'
import wangpan from '@/page/wangpan.vue'
  export default {
    name: "Upload",
    data () {
      return {
        path: "testpath",
        options: {
          // https://github.com/simple-uploader/Uploader/tree/develop/samples/Node.js
          target: '//localhost:8081/upload',
          testChunks: false
        },
        attrs: {
          accept: 'image/*'
        }
      }
    },
    created(){
      console.log(wangpan.data.path);
      this.path = wangpan.path;
    }
  }
</script>

<style>
  .uploader-example {
    width: 101%;
    font-size: 12px;
  }
  .uploader-example .uploader-btn {
    margin-right: 0%
  }
  .uploader-example .uploader-list {
    max-height: 440px;
    overflow: auto;
    overflow-x: hidden;
    overflow-y: auto;
  }
</style>


